html {
  --color-fonts: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  height: 60px;
  line-height: 60px;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  font-size: 12px;
  font-style: normal;
  text-decoration: none;
}

.nav__links {
  text-align: center;
  color: #000;
  text-wrap: nowrap;
  margin-left: 20px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.is-active {
  color: var(--color-fonts);
  margin: 0;
}

.nav__links:hover {
  color: var(--color-fonts);
}

.nav__list .is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--color-fonts);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

